
export default {
	onLaunch: function() {

		uni.getLocation({
			type: 'wgs84',
			success: function(res) {
				let location = {
					longitude: res.longitude,
					latitude: res.latitude
				}
				uni.setStorage({
					key: 'location',
					data: location
				});
			}
		});











	},
	onShow: function() {
		console.log('App Show')
	},
	onHide: function() {
		console.log('App Hide')
	}
}
